import React, {Component} from 'react'
import './vertx.base.less'
import Apis from './Apis'
import {AFetchJSON, AFetchPostJson} from '../common/AFetch'
import {message, Table, Button, Form, Row, Col, Popconfirm, Layout} from 'antd'
const {Header, Content, Footer} = Layout



class App extends Component {

    state = {
        uploading: false,
        dataSource:[],
        selectedFile:'',
        files:[],
    }

    constructor(props) {
        super(props)
        this.columns = [{
            title: '',
            dataIndex: 'name',
            key: 'name',
          }, {
            title: 'ArtifactId',
            dataIndex: 'artifact_id',
            key: 'artifact_id',
          }, {
            title: 'GroupId',
            dataIndex: 'group_id',
            key: 'group_id',
          }, {
            title: 'Version',
            dataIndex: 'version',
            key: 'version',
          }, {
            title: 'Enter',
            dataIndex: 'enter',
            key: 'enter',
          }, {
            title: 'UploadTime',
            dataIndex: 'upload_time',
            key: 'upload_time',
          }, {
            title: 'Action',
            key: 'action',
            render: (text, record) => (
              <span>
              <Popconfirm title="Jar" onConfirm={_ => this.doDelete(record)}  okText="" cancelText="">
                <Button type='danger' size='small' ></Button>
              </Popconfirm>
              </span>
            ),
          }];
    }

    doDelete = (record) => {
        AFetchJSON(Apis.project_server.delete + `${record.name}`).then(json => {
            if(json.code === 0) {
                message.info('Jar')
                this.fetchData()
            } else {
                message.info('Jar' + json.msg)
            }
        })
    }

    doUpload = () => {
        if(!this.state.selectedFile || this.state.selectedFile === '' || this.state.files.length === 0) {
            message.info('')
            return
        }
        if(!this.state.selectedFile.toLowerCase().endsWith('.jar') && !this.state.selectedFile.toLowerCase().endsWith('.js')) {
            message.info('JarJS')
            return
        }
        let s = this.state.selectedFile
        let s0 = s.substr(s.lastIndexOf("/") + 1, s.length)
        s0 = s0.substr(s0.lastIndexOf('\\') + 1, s0.length)
        let formData = new FormData()
        formData.append('files', this.state.files[0], s0)
        this.setState({uploading: true})
        AFetchPostJson(Apis.project_server.upload, formData, {method: 'post'}).then(json => {
            this.setState({uploading: false})
            if(json.code === 0) {
                message.info('')
                this.fetchData()
                this.setState({selectedFile: ''})
            } else {
                message.info('' + json.msg)
            }
        })
    }

    componentDidMount() {
        this.fetchData()
    }

    fetchData = () => {
        AFetchJSON(Apis.project_server.listAll).then(json => {
            if(json.code === 0) {
                let dataSource = json.data.map(item => {
                    let result = {}
                    for(let key in item) {
                        result[key.toLocaleLowerCase()] = item[key]
                    }
                    result.key = result.uuid
                    console.log(result)
                    return result
                }).reverse()
                this.setState({dataSource})
            }
        })
    }

    render() {
        return (
        <Layout className='pfs'>
            <Header className='header'>
                <div className='entry-server-title'>Entry Server</div>
                <div className='file-input-group'>
                    <input ref={ref => this.domFileInput = ref} className='file' type="file" name="videoUrl" onChange={e => {
                        this.setState({selectedFile: e.target.value, files: e.target.files})
                    }} />
                    <Button disabled={this.state.uploading} onClick={_ => this.domFileInput.click()} className='btn' type="primary" >Jar</Button>
                </div>
            </Header>
            <Content>
                <div className='content'>
                <Row>
                    <Col span={12}>
                        {this.state.selectedFile && this.state.selectedFile !== '' && <Button onClick={this.doUpload} style={{float:'right'}} disabled={this.state.uploading} loading={this.state.uploading}>{this.state.selectedFile}</Button>}
                    </Col>
                </Row>
                <div style={{marginTop:10}}>
                    <Table pagination={false} columns={this.columns} dataSource={this.state.dataSource} />
                </div>
                </div>
            </Content>
            <Footer>Joker core plugin entry server.</Footer>
        </Layout>)
    }
}

export default Form.create()(App)


