
import 'whatwg-fetch'

export function AFetch(input, init) {
    // getToken()
    if(!init) init = {}
    return fetch(input, init)
}
export function AFetchPost(input, body, init = {method:'post', headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
}}) {
    // getToken()
    if(!init) init = {}
    init.body = body
    return fetch(input, init)
}
export function AFetchPut(input, body, init = {method:'put', headers: {
}}) {
    // getToken()
    if(!init) init = {}
    init.body = body
    return fetch(input, init)
}
export function AFetchDelete(input, body, init = {method:'delete', headers: {
}}) {
    // getToken()
    if(!init) init = {}
    init.body = body
    return fetch(input, init)
}

export function AFetchJSON(input, init) {
    return AFetch(input, init).then(res => {
        return  res.json()
    }).catch(e => {
        return {code:1, msg:e}
    })
}

export function AFetchPostJson(input, body, init = {method:'post', headers: {
}}) {
    return AFetchPost(input, body, init).then(res => res.json()).catch(e => {
        return {code:1, msg:e}
    })
}

export function AFetchPutJson(input, body, init = {method:'put', headers: {
}}) {
    return AFetchPut(input, body, init).then(res => res.json()).catch(e => {
        return {code:1, msg:e}
    })
}

export function AFetchDeleteJson(input, body, init = {method:'delete', headers: {
}}) {
    return AFetchDelete(input, body, init).then(res => res.json()).catch(e => {
        return {code:1, msg:e}
    })
}


